<template>
<div class="root-container">
  <Menu theme="light" :active-name="currentPage" @on-select="selectChanged">
    <MenuGroup title="分类列表">
      <MenuItem :key="item.id" :name="item.id" v-for="(item, index) in catalog">
        {{item.name}}
      </MenuItem>
    </MenuGroup>
  </Menu>
  <div class="main-container">
    <div class="tool-container">
      <Button type="primary" shape="circle" icon="plus-round" @click="createData">新建数据</Button>
      <!-- <Button type="primary" shape="circle" icon="checkmark-round">保存</Button> -->
    </div>
    <div class="content-container">
      <div class="data-item" v-for="(item, index) in currentCatalog.children">
        <p>{{item}}</p>
        <Button type="error" shape="circle" icon="close-round" size="small" style="margin-left:50px"></Button>
      </div>
    </div>
  </div>
  <Modal
    v-model="isShowNewDataModal"
    title="新建数据"
    @on-ok="ok"
    @on-cancel="cancel">
    <Input v-model="newData" placeholder="请输入" style="width: 200px"></Input>
  </Modal>
</div>
</template>

<script>
export default {
  data() {
    return {
      isShowNewDataModal: false,
      newData: "",
      currentPage: "1",
      currentCatalog: {},
      catalog: [
        {
          id: "1",
          name: "分类1",
          children: ["1111111", "1111111", "1111111"]
        },
        {
          id: "2",
          name: "分类2",
          children: ["22222", "22222", "22222"]
        },
        {
          id: "3",
          name: "分类3",
          children: ["3", "3", "3"]
        }
      ]
    };
  },
  mounted() {
    this.currentCatalog = this.catalog.find(item => {
      return item.id == this.currentPage;
    });
  },
  methods: {
    selectChanged(id) {
      this.currentCatalog = this.catalog.find(item => {
        return item.id == id;
      });
    },
    createData() {
      this.isShowNewDataModal = true;
      this.newData = "";
    },
    ok() {
      this.isShowNewDataModal = false;
      this.$Message.info("添加成功");
    },
    cancel() {
      this.isShowNewDataModal = false;
    }
  }
};
</script>

<style scoped>
.root-container {
  display: flex;
  flex-direction: row;
  height: 100%;
}
.main-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.tool-container {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  border-bottom: 1px solid #bfbfbf;
  padding: 0 30px;
}
.content-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 50px;
}
.data-item {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
}
</style>



